          <div class="form-box">
            <Form v-slot="{ errors }" @submit.prevent="onSubmit" autocomplete="off">
              <!-- /Eamil -->
              <label data-aos="fade-up" for="" class="signUp-label rwd-signUp-label">
                <span class="signUp-span"><span class="text-danger">*</span>Email</span>
                <Field v-model="signUpData.email" class="signUp-input mb-15" :class="{ 'border-danger': errors[''] }" name="" type="email" rules="email|required" placeholder=" Email" />
                <error-message name="" class="error-text"></error-message>
              </label>

              <!-- /Email -->
              <label data-aos="fade-up" for="" class="signUp-label py-0 pb-50">
                <span class="signUp-span-item"><span class="text-danger">*</span> Email</span>
                <Field v-model="signUpData.email_confirmation" class="signUp-input mb-15" rules="required|emailConfirmed:@" :class="{ 'border-danger': errors[''] }" name="" type="email" placeholder=" Email" />
                <error-message name="" class="error-text"></error-message>
              </label>

              <!--  -->
              <label data-aos="fade-up" for="" class="signUp-label py-0 pb-50">
                <span class="signUp-span-item"></span>
                <Field v-model="signUpData.account" class="signUp-input mb-15" name="" type="email" placeholder="()Email" />
              </label>

              <!--  -->
              <label data-aos="fade-up" for="" class="signUp-label py-0 pb-50">
                <span class="signUp-span-item"><span class="text-danger">*</span></span>
                  <Field v-model="signUpData.password" class="login-password-input signUp-Password mb-15" :class="{ 'border-danger': errors[''] }" maxlength="13" rules="required|min:8" name="" type="password" placeholder=""/>
                  <error-message name="" class="error-text"></error-message>
              </label>

              <!--  -->
              <label data-aos="fade-up" class="signUp-label py-0" for="">
                <span class="signUp-span-item"><span class="text-danger">*</span></span>
                <Field v-model="signUpData.password_confirmation" maxlength="13" rules="required|passwordConfirmed:@" :class="{ 'border-danger': errors[''] }" class="signUp-input signUp-ConPassword" name="" type="password" placeholder="" />
              </label>

              <!--  -->
              <div class="form-check show-password rwd-show-password">
                <div>
                <error-message name="" class="error-text error-text-rwd"></error-message>
                </div>
                <div class="d-flex align-items-center" >
                  <input data-aos="fade-up" class="form-check-input" type="checkbox" @click="showPassword('signUp-Password')">
                  <label data-aos="fade-up" class="form-check-label">
                    
                  </label>
                </div>
              </div>

              <!--  -->
              <label data-aos="fade-up" for="" class="signUp-label py-0 pb-50">
                <span class="signUp-span-item"></span>
                <Field v-model="signUpData.name" class="signUp-input mb-15" name="" type="text" placeholder="()" />
              </label>

              <!--  -->
              <label data-aos="fade-up" class="signUp-label py-0 pb-50">
                <span class="signUp-span-item"><span class="text-danger">*</span></span>
                <i class="fas fa-caret-down select-down fa-2x"></i>
                <Field as="select" name="" v-model="signUpData.gender" rules="required" :class="{ 'border-danger': errors[''] }" class="select-gender mb-15">
                  <option value="" disabled></option>
                  <option value="male"></option>
                  <option value="female"></option>
                  <option value="other"></option>
