                <Field name="" type="date" v-model="signUpData.birthday" class="signUp-input mb-15" rules="required" :class="{ 'border-danger': errors[''] }"/>
                <error-message name="" class="error-text"></error-message>
              </label>

              <!--  -->
              <label data-aos="fade-up" class="signUp-label py-0 pb-25">
                <span class="signUp-span-item"><span class="text-danger">*</span></span>
                <i class="fas fa-caret-down select-down fa-2x"></i>
                <Field data-aos="fade-up" name="" as="select" v-model="signUpData.city" :class="{ 'border-danger': errors[''] }" class="select-gender mb-15" rules="required">
                  <option selected disabled value="" ></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </Field>
              <error-message name="" class="error-text"></error-message>
              </label>
              <!--  -->
              <div data-aos="fade-up" class="singUp-box">
                <h4 class="pb-25"></h4>
                <button class="signUp-btn"></button>
                <p class="agree-text">
                  <a @click="hideHeader" data-bs-toggle="modal" data-bs-target="#personalModal"></a>
                </p>
              </div>
            </Form>
          </div>
        </div>
      </div>
    </section>

    <!--  modal -->
    <div class="modal fade personalModal" data-bs-backdrop="static" id="personalModal" tabindex="-1" aria-labelledby="personalModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-body">
            <p></p><br>
            <p></p><br>
            <p></p><br>
            <p>E-mail</p><br>
            <p></p><br>
            <p></p><br>
            <p></p><br>
            <p>3//</p><br>
            <p></p><br>
            <p>///</p><br>
            <p>(04)2206-21212261<br>
             Email  swr@mail.cmuh.org.tw </p>
            <div class="mt-60">
              <p></p>
              <p></p>
              <p></p>
            </div>
          </div>
          <div class="modal-footer">
            <span @click="hideHeader('close')" data-bs-toggle="modal" class="close-modal">OK</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
// import Qs from 'qs'

// 
import { defineRule } from 'vee-validate'

// custom 
defineRule('passwordConfirmed', (value, [target]) => {
  if (value === target) {
    return true
  }
  return ''
})

defineRule('emailConfirmed', (value, [target]) => {
  if (value === target) {
    return true
  }
  return ''
})

defineRule('selectLocal', (value, [target]) => {
  if (value === target) {
    return true
  }
  return ''
})

export default {
  data () {
    return {
      isActive: false,
      errorClass: 'errorClass',
      loginData: {
        email: '',
        password: ''
      },
      ajaxData: '',
      signUpData: {
        email: '',
        email_confirmation: '',
        account: '',
        password: '',
        password_confirmation: '',
        name: '',
        gender: '',
        birthday: '',
        city: ''
      },
      window: {
        width: 375
      },
      config: {
        weekNumbers: true
      },
      query: {}
    }
  },
  components: {
    // flatPickr
  },
  methods: {
    showPassword (e) {
      const loginPasswordValue = document.getElementById('login-password')
      const signUpPasswordValue = document.querySelector('.signUp-Password')
      const signUpConPassword = document.querySelector('.signUp-ConPassword')
      if (e === 'login-password' && loginPasswordValue.type === 'password') {
        loginPasswordValue.type = 'text'
      } else {
        loginPasswordValue.type = 'password'
      }

      if (e === 'signUp-Password' && signUpPasswordValue.type === 'password') {
        signUpPasswordValue.type = 'text'
        signUpConPassword.type = 'text'
      } else {
        signUpPasswordValue.type = 'password'
        signUpConPassword.type = 'password'
      }
    },
    onSubmit () {
      const vm = this
      vm.$http.post('https://iecosystem-api.tomyue.cc/api/register', vm.signUpData).then((res) => {
        if (res.data.success) {
          console.log(res)
          this.$swal.fire('')
        } else {
          console.log(res)
          this.$swal.fire('')
        }
      })
    },
    login () {
      const vm = this
      vm.$store.dispatch('login', {
        email: vm.loginData.email,
        password: vm.loginData.password
      }).then((res) => {
        this.$router.push('/backed')
      }).catch(() => {
        this.$swal.fire('')
      })
    },
    hideHeader (e) {
      $('.header').hide()
      if (e === 'close') {
        $('.header').show()
      }
    },
    myEventHandler (e) {
      this.window.width = window.innerWidth
    },
    changeSignUp () {
      $('.login-left').toggleClass('d-none')
      $('.login-right').toggleClass('d-block')
    },
    lineLogin () {
      const link = 'https://iecosystem-api.tomyue.cc/api/line/auth'
      // link += 'response_type=code'
      // link += `&client_id=${process.env.VUE_APP_LINE_CHANELL_ID}`
      // link += '&redirect_uri=http://localhost:8080/'
      // link += '&state=login'
      // link += '&scope=openid%20profile'
      window.location.href = link
      // this.$route.push({ path: 'LineLogin' })
      // const option = Qs.stringify({
      //   grant_type: 'authorization_code',
      //   code: 'OamJBaBgh8BkUKgS1C6f',
      //   redirect_uri: process.env.VUE_APP_LINE_REDIRECT_URL,
      //   client_id: process.env.VUE_APP_LINE_CHANELL_ID,
      //   client_secret: process.env.VUE_APP_LINE_CHANELL_SECRET
      // })
    }
  },
  watch: {
    'signUpData.city' (value) {
      if (value === '') {
        this.isActive = false
      }
    }
  },
  mounted () {
    this.window.width = window.innerWidth
    this.query = this.$route.query
    if (this.query.token !== '') {
      console.log('123')
    }
    console.log(this.query.token)
  },
  created () {
    localStorage.removeItem('user')
    window.addEventListener('resize', this.myEventHandler)
  }
}
</script>
